'use client';
import React, { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import Image from 'next/image';
import { 
  LayoutDashboard, 
  UserPlus, 
  Calendar, 
  Users, 
  ClipboardList,
  Settings,
  LogOut,
  ChevronLeft,
  ChevronRight,
  Activity,
  Bell,
  FileText,
  UserCheck,
  CalendarCheck
} from 'lucide-react';
import { supabase } from '@/src/lib/supabase';
import { useRouter } from 'next/navigation';

interface NavItem {
  href: string;
  label: string;
  icon: React.ReactNode;
  color: string;
  badge?: string;
}

const ReceptionistSidebar: React.FC = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const pathname = usePathname();
  const router = useRouter();

  const navItems: NavItem[] = [
    { 
      href: '/receptionist/dashboard', 
      label: 'Dashboard', 
      icon: <LayoutDashboard size={18} />, 
      color: 'text-blue-600'
    },
    { 
      href: '/receptionist/patients/register', 
      label: 'Register Patient', 
      icon: <UserPlus size={18} />, 
      color: 'text-green-600'
    },
    { 
      href: '/receptionist/patients', 
      label: 'Patient Records', 
      icon: <Users size={18} />, 
      color: 'text-purple-600',
      badge: '1,284'
    },
    { 
      href: '/receptionist/appointments', 
      label: 'Appointments', 
      icon: <Calendar size={18} />, 
      color: 'text-orange-600',
      badge: '42'
    },
    { 
      href: '/receptionist/appointments/schedule', 
      label: 'Schedule Appointment', 
      icon: <CalendarCheck size={18} />, 
      color: 'text-teal-600'
    },
    { 
      href: '/receptionist/patients/vitals', 
      label: 'Record Vitals', 
      icon: <Activity size={18} />, 
      color: 'text-red-600'
    },
    { 
      href: '/receptionist/reports', 
      label: 'Reports', 
      icon: <FileText size={18} />, 
      color: 'text-indigo-600'
    },
    { 
      href: '/receptionist/notifications', 
      label: 'Notifications', 
      icon: <Bell size={18} />, 
      color: 'text-pink-600',
      badge: '5'
    },
  ];

  const handleLogout = async () => {
    try {
      await supabase.auth.signOut();
      router.push('/login');
    } catch (error) {
      console.error('Logout error:', error);
    }
  };

  return (
    <aside className={`${isCollapsed ? 'w-16' : 'w-64'} bg-white border-r border-gray-200 h-full flex flex-col shadow-sm transition-all duration-300 ease-in-out`}>
      {/* Header */}
      <div className="h-20 border-b border-gray-100 flex items-center justify-between px-4">
        <div className={`flex items-center ${isCollapsed ? 'justify-center w-full' : ''}`}>
          <div className="flex items-center justify-center">
            <Image
              src="/logo/annamHospital-bg.png"
              alt="Annam Hospital"
              width={48}
              height={48}
              className="w-12 h-12 object-contain"
            />
          </div>
          {!isCollapsed && (
            <div className="ml-3">
              <div className="flex items-center">
                <UserCheck className="h-4 w-4 text-blue-500 mr-1" />
                <span className="text-xs font-semibold text-blue-600 uppercase tracking-wide">Front Desk</span>
              </div>
            </div>
          )}
        </div>
        
        {!isCollapsed && (
          <button 
            onClick={() => setIsCollapsed(!isCollapsed)}
            className="p-1.5 hover:bg-gray-100 rounded-lg transition-colors duration-200"
          >
            <ChevronLeft size={16} className="text-gray-500" />
          </button>
        )}
      </div>

      {/* Expand button for collapsed state */}
      {isCollapsed && (
        <div className="px-2 py-2 border-b border-gray-100">
          <button 
            onClick={() => setIsCollapsed(!isCollapsed)}
            className="w-full p-1.5 hover:bg-gray-100 rounded-lg transition-colors duration-200 flex justify-center"
          >
            <ChevronRight size={16} className="text-gray-500" />
          </button>
        </div>
      )}
      
      {/* Navigation */}
      <nav className="flex-1 px-2 py-4 space-y-1">
        {navItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.href}
              href={item.href}
              className={`group flex items-center px-3 py-2.5 rounded-xl transition-all duration-200 relative overflow-hidden ${
                isActive 
                  ? 'bg-gradient-to-r from-blue-50 to-blue-100 text-blue-600 shadow-sm' 
                  : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900'
              }`}
            >
              {/* Active indicator */}
              {isActive && (
                <div className="absolute left-0 top-0 bottom-0 w-0.5 bg-blue-500 rounded-r-full"></div>
              )}
              
              {/* Icon */}
              <div className={`flex-shrink-0 ${isActive ? 'text-blue-600' : item.color} transition-colors duration-200`}>
                {item.icon}
              </div>
              
              {/* Label and Badge */}
              {!isCollapsed && (
                <div className="flex items-center justify-between w-full ml-3">
                  <span className="font-medium text-sm">{item.label}</span>
                  {item.badge && (
                    <span className={`px-2 py-0.5 text-xs font-semibold rounded-full ${
                      isActive 
                        ? 'bg-blue-200 text-blue-700' 
                        : 'bg-gray-200 text-gray-600'
                    }`}>
                      {item.badge}
                    </span>
                  )}
                </div>
              )}
            </Link>
          );
        })}
      </nav>
      
      {/* User Profile Section */}
      {!isCollapsed && (
        <div className="px-2 py-3 border-t border-gray-100">
          <div className="flex items-center space-x-3 p-2.5 bg-gradient-to-r from-blue-50 to-indigo-50 rounded-xl border border-blue-200">
            <div className="w-8 h-8 bg-gradient-to-r from-blue-500 to-indigo-500 rounded-full flex items-center justify-center text-white font-semibold text-xs">
              <UserCheck size={16} />
            </div>
            <div className="flex-1">
              <p className="font-semibold text-gray-900 text-sm">Anna Receptionist</p>
              <p className="text-xs text-blue-600 font-medium">Front Desk Operations</p>
            </div>
          </div>
        </div>
      )}
      
      {/* Bottom Actions */}
      <div className="px-2 py-3 border-t border-gray-100 space-y-1">
        <button className="w-full flex items-center px-3 py-2.5 text-gray-600 hover:bg-gray-50 hover:text-gray-900 rounded-xl transition-all duration-200">
          <Settings size={18} />
          {!isCollapsed && <span className="ml-3 font-medium text-sm">Settings</span>}
        </button>
        
        <button 
          onClick={handleLogout}
          className="w-full flex items-center px-3 py-2.5 text-red-600 hover:bg-red-50 rounded-xl transition-all duration-200"
        >
          <LogOut size={18} />
          {!isCollapsed && <span className="ml-3 font-medium text-sm">Logout</span>}
        </button>
      </div>
    </aside>
  );
};

export default ReceptionistSidebar; 